import React from 'react';
import { useNavigate } from 'react-router-dom';
import { isAndroid } from "react-device-detect";
interface CameraProps {
  apiData: any;
  lang: any;
}
const FullPageVideo: React.FC<CameraProps> = ({apiData,lang}) => {
  const navigate = useNavigate();

  

  const handleVideoEnd = () => {
    // เปลี่ยนหน้าเมื่อวิดีโอเล่นจบ
    if(isAndroid){
      navigate('/'+lang+'/next-page');

    }else{
      navigate('/'+lang+'/camera');
    }
  };

  return (
    <div className="w-full h-screen flex justify-center items-center bg-black relative">
      {/* วิดีโอ */}
      <video
        src={apiData}
        autoPlay
        loop={false} // วิดีโอเล่นจบแล้วไม่ต้องวนซ้ำ
        muted
        playsInline
        className="w-full h-full object-cover"
        onEnded={handleVideoEnd} // เรียกฟังก์ชันเมื่อเล่นจบ
      ></video>

      {/* เนื้อหาซ้อน */}
      
    </div>
  );
};

export default FullPageVideo;
