import React, { useEffect, useState } from "react";
import { useLocation,Link } from "react-router-dom";

interface CameraProps {
  textdis2: string;
  textdis3:string;
  lang:any;
}

const SocialShare: React.FC<CameraProps> = ({textdis2,textdis3,lang}) => {
  const { state } = useLocation();
  const [imageClass, setImageClass] = useState<string>("");
  const [spinner, setSpinner] = useState<boolean>(false);

  const { imageData, videoDataUrl } = state || {};

  useEffect(() => {
    if (imageData) {
      const img = new Image();
      img.src = imageData;
      img.onload = () => {
        if(img.width > img.height) {
            setImageClass("w-64 h-40");
            setSpinner(true);
        }else if( img.width< img.height){
            setImageClass("w-40 h-64");
            setSpinner(true);
        }
      };
    }
    // เช็คขนาดของวิดีโอ
    if (videoDataUrl) {
        const video = document.createElement("video");
        video.src = videoDataUrl;
  
        video.onloadedmetadata = () => {
          if (video.videoWidth > video.videoHeight) {
            // วิดีโอมีความกว้างมากกว่าความสูง
            setImageClass("w-64 h-40");
          } else if (video.videoWidth < video.videoHeight) {
            // วิดีโอมีความสูงมากกว่าความกว้าง
            setImageClass("w-40 h-64");
          }
          setSpinner(true);
        };
      }
    if (videoDataUrl) {
      setSpinner(true);
    }
  }, [imageData, videoDataUrl]);

  const lineUrl = `https://line.me/R/share?text=${encodeURIComponent(
    `Check out this amazing content!`
  )}`;
  const facebookUrl = `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(
    imageData || videoDataUrl
  )}`;
  const instagramUrl = `https://www.instagram.com/?url=${encodeURIComponent(
    imageData || videoDataUrl
  )}`;
    const downloadImage = () => {
        if(imageData != null){
            const link = document.createElement("a"); // สร้าง element <a>
            link.href = imageData; // กำหนด URL รูปภาพ
            link.download = "camerashare.png"; // ชื่อไฟล์ที่จะบันทึก
            link.click(); // ทำการคลิกลิงก์
        }else{
            const link = document.createElement("a"); // สร้าง element <a>
            link.href = videoDataUrl; // กำหนด URL รูปภาพ
            link.download = "camerashare.mp4"; // ชื่อไฟล์ที่จะบันทึก
            link.click(); // ทำการคลิกลิงก์
        }
        
    };
  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-gray-100 space-y-6">
      {spinner === false && (
        <div className="text-center">
          <div role="status">
            {/* Loading Spinner */}
          </div>
        </div>
      )}
      {spinner === true && imageData && (
        <div className={imageClass}>
          <img
            src={imageData}
            alt="Image to share"
            className="w-full h-full object-cover rounded-lg shadow-lg"
          />
        </div>
      )}
      {spinner === true && videoDataUrl && (
        <div className={imageClass}>
        <video autoPlay controls loop muted className="w-full h-full">
          <source src={videoDataUrl} type="video/mp4" />
          Your browser does not support the video tag.
        </video>
      </div>
      )}
      {spinner === true && 
        <div className="flex flex-col items-center space-y-4">
            {/* Line */}
            {/* <a
            href={lineUrl}
            target="_blank"
            rel="noopener noreferrer"
            className="hover:opacity-75"
            >
            <img
            src="line.png" // ไอคอน Line
            alt="Share on Line"
            className="w-16 h-16"
            />
            </a> */}

            {/* Facebook */}
            {/* <a
            href={facebookUrl}
            target="_blank"
            rel="noopener noreferrer"
            className="hover:opacity-75"
            >
            <img
                src="https://cdn-icons-png.flaticon.com/512/733/733547.png" // ไอคอน Facebook
                alt="Share on Facebook"
                className="w-16 h-16"
            />
            </a> */}

            {/* Instagram */}
            {/* <a
            href={instagramUrl}
            target="_blank"
            rel="noopener noreferrer"
            className="hover:opacity-75"
            >
            <img
                src="https://cdn-icons-png.flaticon.com/512/733/733558.png" // ไอคอน Instagram
                alt="Share on Instagram"
                className="w-16 h-16"
            />
            </a> */}
            <label className="text-center">
              {textdis2}
            </label>

            {/* ปุ่มดาวน์โหลด */}
            <a
              onClick={downloadImage}
              target="_blank"
              rel="noopener noreferrer"
              className="hover:opacity-75"
            >
              <img
                src="../downloading.png" // ไอคอนดาวน์โหลด
                alt="Download Content"
                className="w-16 h-16"
              />
            </a>
            
        </div>
      }
      {/* ปุ่มกลับไปที่หน้า Camera */}
      <Link to={"/"+lang+"/camera"} onClick={() => {
    window.location.href="/"+lang+"/camera"; // รีเฟรชหน้า
  }}
   className="mt-6 p-2 " style={{backgroundColor:"orange"}}>
        
        {textdis3}
      </Link>
    </div>
  );
};

export default SocialShare;