import React, { useEffect,useState } from 'react';
import { BrowserRouter, Routes, Route,useParams } from 'react-router-dom';
import FullPageVideo from './components/FullPageVideo';
import AllowCamera from './components/AllowCamera';
import SocialShare from './components/SocialShare';
import Camera from './components/Camera'
import DeviceInfo  from './components/DeviceInfo';

import Templates from './components/Templates';

const NextPage: React.FC = () => {
  return (
    <div className="w-full h-screen flex justify-center items-center bg-green-500">
      <h1 className="text-white text-4xl font-bold">Welcome to the Next Page!</h1>
    </div>
  );
};

function App() {
  const apiUrl = process.env.REACT_APP_API_URL;
  const { lang } = useParams();
  const [videolink,setvideolink] = useState<string>("");
  const [text1,settext1] = useState<string>("");
  const [frame1,setframe1] = useState<string>("");
  const [frame2,setframe2] = useState<string>("");
  const [text2,settext2] = useState<string>("");
  const [text3,settext3] = useState<string>("");
  const [langg,setlangg] = useState<string>("");
  const getLangFromURL = () => {
    const path = window.location.pathname; // ดึง path ทั้งหมด
    const lang = path.split('/')[1]; // แยกตาม "/" แล้วดึงค่า lang
    return lang;
  };
  console.log("api",apiUrl);
  useEffect(()=>{
    setlangg(getLangFromURL());
    document.title = "Kubota-Camera";
    const fetchData = async () => {
      const response = await fetch("https://kubota.poom.posorange.com/api/FrameAPI/GetFrameAll?type="+langg);
      const result = await response.json();
      console.log(response);
      if(response.status === 200){
        setvideolink(result.getFrameAll[0].frameVideo)
        setframe1(result.getFrameAll[0].framePath)
        setframe2(result.getFrameAll[0].framePaths)
        settext1(result.getFrameAll[0].openCamera)
        settext2(result.getFrameAll[0].download)
        settext3(result.getFrameAll[0].backCamera)
      }
    };
    fetchData();
  },[langg]);
  return (
    <BrowserRouter>
      <Routes>
        <Route path="/:lang" element={<FullPageVideo lang={langg} apiData={videolink === "" ? "../IntroKubota5s_1-2.mp4" : "https://kubota.poom.posorange.com/Video/"+videolink} />}  />
        <Route path="/:lang/next-page" element={<AllowCamera lang={langg} apiData={text1 === "" ? "เปิดใช้กล้อง" : text1} />} />
        <Route path="/:lang/templates" element={<Templates />} />
        <Route path="/:lang/camera" element={<Camera lang={langg} frame1={frame1 === "" ? "../Frame1.png" : "https://kubota.poom.posorange.com/FrameImage/"+frame1} frame2={frame2 === "" ? "../Frame2.png" : "https://kubota.poom.posorange.com/FrameImage/"+frame2} />} key={Date.now()} />
        <Route path="/:lang/social" element={<SocialShare lang={langg} textdis2={text2 === "" ? "กดปุ่มเพื่อดาวน์โหลด":text2} textdis3={text3 === "" ? "กลับไปที่หน้ากล้อง":text3} />} />
        <Route path="/:lang/deviceinfo" element={<DeviceInfo />} />
      </Routes>
    </BrowserRouter>
  );
}

export default App;