import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { isAndroid, isIOS, isDesktop } from "react-device-detect";
interface CameraProps {
  apiData: any;
  lang:any;
}
const AllowCamera: React.FC<CameraProps> = ({apiData,lang}) => {
  let device = "Unknown";
  if (isAndroid) {
    device = "Android";
  } else if (isIOS) {
    device = "iOS";
  } else if (isDesktop) {
    device = "Desktop";
  }
 
  const [isCameraAllowed, setIsCameraAllowed] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [videoDevices, setVideoDevices] = useState<MediaDeviceInfo[]>([]);
  const [currentDeviceId, setCurrentDeviceId] = useState<string | null>(null);
  const [userCamera, setUserCamera] = useState<MediaDeviceInfo | null>(null);
  const [environmentCamera, setEnvironmentCamera] = useState<MediaDeviceInfo | null>(null);
  const [rotange,setrotange] = useState<boolean>(true);
  const navigate = useNavigate();

  const stopExistingStream = () => {
    const videoElement = document.querySelector("video");
    if (videoElement && videoElement.srcObject) {
      const stream = videoElement.srcObject as MediaStream;
      stream.getTracks().forEach((track) => track.stop());
      videoElement.srcObject = null;
    }
  };

  const getVideoDevices = async () => {
    try {
      const devices = await navigator.mediaDevices.enumerateDevices();
      const videoDevices = devices.filter(device => device.kind === 'videoinput');
      setVideoDevices(videoDevices);

      // แยกกล้องหน้าและกล้องหลัง
      const userCamera = videoDevices.find(device => 
        device.label.toLowerCase().includes('front') || device.label.toLowerCase().includes('user')
      );
      const environmentCamera = videoDevices.find(device => 
        device.label.toLowerCase().includes('back') || device.label.toLowerCase().includes('environment')
      );
      
      setUserCamera(userCamera || null);
      setEnvironmentCamera(environmentCamera || null);
    } catch (error) {
      console.error('Error enumerating devices:', error);
    }
  };

  const startCamera = async (deviceId: string) => {
    try {
      stopExistingStream(); // หยุด Stream เดิมก่อน
      const stream = await navigator.mediaDevices.getUserMedia({
        video: { deviceId },
      });
      setIsCameraAllowed(true);
      setError(null);

      const videoElement = document.querySelector("video");
      if (videoElement) {
        videoElement.srcObject = stream;
        videoElement.play();
      }
    } catch (err: any) {
      setError(err.message || "Failed to access camera");
    }
  };

  const switchCamera = async () => {
    if (userCamera && environmentCamera) {
      const newDeviceId = currentDeviceId === userCamera.deviceId ? environmentCamera.deviceId : userCamera.deviceId;
      setCurrentDeviceId(newDeviceId);
      await startCamera(newDeviceId);
      if(rotange === true){
        setrotange(false);

      }else{
        setrotange(true);

      }
    } else {
      console.log("ไม่มีกล้องเพียงพอเพื่อสลับ");
    }
  };

  const nexttocamer = async () => {
    stopExistingStream();
    navigate("/"+lang+"/camera");
  };

  useEffect(() => {
    getVideoDevices();
  }, []);

  useEffect(() => {
    if (userCamera && environmentCamera && !currentDeviceId) {
      // เริ่มต้นใช้กล้องหน้า (userCamera) หากไม่พบการเลือก deviceId
      setCurrentDeviceId(userCamera.deviceId);
      startCamera(userCamera.deviceId);
      console.log(userCamera);
      console.log(environmentCamera);
      console.log(currentDeviceId);
    }
  }, [userCamera, environmentCamera, currentDeviceId]);

  return (
    <div className="w-full h-screen flex flex-col items-center justify-center bg-gray-100" style={{background: 'url("Kubotaแนวตั้ง 1.png") center/cover no-repeat'}}>
  
  {/* ปุ่ม Allow Camera */}
  <button
    style={{ backgroundColor: "gray" }}
    onClick={() => startCamera(userCamera?.deviceId || "")}
    className="px-6 py-3 bg-blue-500 text-white font-bold rounded-lg hover:bg-blue-600"
  >
    {apiData}
  </button>
  
  {error && <p className="mt-4 text-red-600">{error}</p>}

  <video className="mt-4 w-3/4" style={{width:"50%",transform: rotange === true ? 'scaleX(-1)' : 'none'}} autoPlay muted ></video>

  {/* ใช้ flex เพื่อจัดปุ่ม Switch Camera และ Next camera ในบรรทัดเดียวกัน */}
  {isCameraAllowed && (
    <div className="relative w-full h-screen flex items-center justify-center bg-gray-100" style={{height:"20vh",paddingTop:"25%"}}>
    {/* ปุ่ม Next Camera ตรงกลาง */}
    <button
      style={{
        width: '100px',
        height: '100px',
        borderRadius: '5%',
        background: `url("../ok.png") center/cover no-repeat`,
        border: 'none',
        boxShadow: '0 4px 6px rgba(255, 255, 255, 0.3)',
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)', // ทำให้ปุ่มอยู่ตรงกลาง
      }}
      onClick={nexttocamer}
    >
    </button>
  
    {/* ปุ่ม Switch Camera อยู่ขวามือ */}
    <button
      onClick={switchCamera}
      className="px-6 py-3 bg-green-500 text-white font-bold rounded-lg hover:bg-green-600 absolute top-1/2 right-4 transform -translate-y-1/2"
      style={{
        background: "url('../flip_camera_ios.png') center/cover no-repeat",
        width: '50px',
        height: '50px',
        borderRadius: '5%',
        border: 'none',
        boxShadow: '0 4px 6px rgba(255, 255, 255, 0.3)',
      }}
    >
    </button>
  </div>
  )}
</div>
    
  );
};

export default AllowCamera;