    //         navigate("/social", { state: { videoDataUrl } }); // ส่งข้อมูลวิดีโอ base64 หรือ URL ไปยังหน้า SocialShare
    //       }
    //       setIsRecord(false);
    //     };
    
    //     // เริ่มบันทึกวิดีโอ
    //     mediaRecorderRef.current.start();
    //     console.log("Recording with Cover started");
    
    //     // หยุดบันทึกหลัง 10 วินาที
    //     setTimeout(() => {
    //       if (mediaRecorderRef.current?.state === "recording") {
    //         mediaRecorderRef.current.stop();
    //         console.log("Recording stopped");
    //       }
